// app/utils/voyageStore.ts
import { S3Client, GetObjectCommand, PutObjectCommand } from "@aws-sdk/client-s3";
import { getSignedUrl } from "@aws-sdk/s3-request-presigner";

export const s3 = new S3Client({
  region: "eu-west-3",
  credentials: {
    accessKeyId: process.env.ACCESS_KEY_ID!,
    secretAccessKey: process.env.SECRET_ACCESS_KEY!,
  },
});
export const BUCKET = process.env.BUCKET_NAME!;
const KEY = "voyages.json";

export type PieceJointe = {
  filename: string;
  s3Key: string;
  type: string;
};

export type VoyageEntry = {
  id: string;
  prenom: string;
  nom: string;
  email: string;
  direction_cible: "direction_ecole" | "direction_college" | "direction_lycee";
  type_activite: "voyage" | "sortie" | "autre";
  destination: string;
  objectifs: string;
  date_depart: string;
  date_retour: string;
  effectif_eleves: number;
  effectif_accompagnateurs: number;
  pieces_jointes?: PieceJointe[];
  etat: "etape_1" | "etape_2" | "etape_3" | "refuse" | "valide";
  date_creation: string;
  historique: { action: string; date: string; par: string }[];
};

// ðŸ“¦ Lecture S3
export async function readVoyages(): Promise<VoyageEntry[]> {
  try {
    const obj = await s3.send(new GetObjectCommand({ Bucket: BUCKET, Key: KEY }));
    const body = await obj.Body?.transformToString();
    return body ? JSON.parse(body) : [];
  } catch (err: any) {
    if (err.$metadata?.httpStatusCode === 404) return [];
    throw err;
  }
}

// ðŸ’¾ Ã‰criture S3
export async function writeVoyages(entries: VoyageEntry[]) {
  await s3.send(new PutObjectCommand({
    Bucket: BUCKET,
    Key: KEY,
    Body: JSON.stringify(entries, null, 2),
    ContentType: "application/json",
  }));
}

// âž• Ajouter un voyage
export async function addVoyage(entry: VoyageEntry) {
  const voyages = await readVoyages();
  voyages.push(entry);
  await writeVoyages(voyages);
}

// âŒ Supprimer un voyage
export async function removeVoyage(id: string) {
  const voyages = await readVoyages();
  const filtered = voyages.filter(v => v.id !== id);
  await writeVoyages(filtered);
}

// ðŸ”‘ URL prÃ©-signÃ©e upload
export async function getUploadURL(filename: string): Promise<{ url: string; key: string }> {
  const key = `voyages_files/${Date.now()}_${filename}`;
  const command = new PutObjectCommand({ Bucket: BUCKET, Key: key });
  const url = await getSignedUrl(s3, command, { expiresIn: 3600 });
  return { url, key };
}

// ðŸ”‘ URL prÃ©-signÃ©e download
export async function getDownloadURL(key: string): Promise<string> {
  const command = new GetObjectCommand({ Bucket: BUCKET, Key: key });
  return await getSignedUrl(s3, command, { expiresIn: 3600 });
}
