import { NextRequest, NextResponse } from "next/server";
import { addVoyage, VoyageEntry } from "@/app/utils/voyageStore";
import { v4 as uuidv4 } from "uuid";
import nodemailer from "nodemailer";

export async function POST(req: NextRequest) {
  try {
    const data = await req.json();
    const {
      prenom,
      nom,
      email,
      direction_cible,
      type_activite,
      destination,
      objectifs,
      date_depart,
      date_retour,
      effectif_eleves,
      effectif_accompagnateurs,
      pieces_jointes
    } = data;

    const voyage: VoyageEntry = {
      id: uuidv4(),
      prenom,
      nom,
      email,
      direction_cible,
      type_activite,
      destination,
      objectifs,
      date_depart,
      date_retour,
      effectif_eleves,
      effectif_accompagnateurs,
      pieces_jointes,
      etat: "etape_1",
      date_creation: new Date().toISOString(),
      historique: [{ action: "crÃ©ation", date: new Date().toISOString(), par: prenom }],
    };

    await addVoyage(voyage);
    const transporter = nodemailer.createTransport({
      host: "smtp.gmail.com",
      port: 465,
      secure: true,
      auth: { 
        user: process.env.SMTP_USER,
        pass: process.env.SMTP_PASS
      },
    });
    const directionMailMap: Record<string, string> = {
      direction_ecole: "florian@h-me.fr",
      direction_college: "direction_college@example.com",
      direction_lycee: "direction_lycee@example.com",
    };

    const to = directionMailMap[direction_cible] || "direction_ecole@example.com";

    await transporter.sendMail({
      from: process.env.SMTP_USER,
      to,
      subject: `[Voyage] Nouvelle demande par ${prenom} ${nom}`,
      text: `
            Bonjour,

            Une nouvelle demande de ${type_activite} a Ã©tÃ© crÃ©Ã©e par ${prenom} ${nom}.

            Destination : ${destination}
            Objectifs pÃ©dagogiques : ${objectifs}
            Dates : ${date_depart} â†’ ${date_retour}
            Nombre d'Ã©lÃ¨ves : ${effectif_eleves}
            Nombre d'accompagnateurs : ${effectif_accompagnateurs}

            Merci de vous connecter pour valider la demande.
          `
    });

    return NextResponse.json({ success: true, voyage });
  } catch (err) {
    return NextResponse.json({ error: err instanceof Error ? err.message : "Erreur serveur" }, { status: 500 });
  }
}
